.cvContainer {
    max-width: 800px;
    margin: 10px auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    line-height: 1.6;
    /*for identifying section*/
    background:  rgba(4,186,192,1);
    border-radius: 20px;
}
  
.cvHeader {
    text-align: center;
    margin-bottom: 20px;

    background: linear-gradient(120deg, rgba(4,186,192,1) 10%, rgba(9,9,121,1) 80%, rgba(4,186,192,1) 100%);
    border-radius: 20px;
}


  
.cvSection {
    margin-bottom: 20px;
    /*for identifying section*/
    background: linear-gradient(120deg, rgba(4,186,192,1) 10%, rgba(9,9,121,1) 80%, rgba(4,186,192,1) 100%);
    border-radius: 20px;
}
  
.cvSection h2 {
    border-bottom: 2px solid #000;
    padding-bottom: 5px;
    /*for identifying section*/
    background: rgba(9,9,121,1) 80%;
    border-radius: 20px;
}
  
.cvSection ul {
    list-style-type: none;
    padding: 10px;
    /*for identifying section*/
    background: linear-gradient(120deg,rgba(2,0,36,1) 10%, rgba(9,9,121,1) 60%);
    border-radius: 20px;
}
  
  .cvSection li {
    margin-bottom: 10px;
    /*for identifying section*/
    background: linear-gradient(120deg, rgba(4,186,192,1) 60%, rgba(9,9,121,1) 100%);
    border-radius: 20px;
}